import Slider from './slider';

export default class MainSlider extends Slider {
  /**
   * ÐÐÐÐÐÐ«Ð Ð¡ÐÐÐÐÐÐ 
   *
   * options   -> Ð¾Ð¿ÑÐ¸Ð¸ Ð´Ð»Ñ ÑÐ°Ð±Ð¾ÑÑ ÑÐ»Ð°Ð¹Ð´ÐµÑÐ° (ÑÐ¼Ð¾ÑÑÐ¸ÑÐµ Ð¿Ð¾Ð´ÑÐ¾Ð±Ð½Ð¾ Ð² ÐºÐ»Ð°ÑÑÐµ Slider)
   */

  /** ===========================
   *        ÐÐ¾Ð½ÑÑÑÑÐºÑÐ¾Ñ        *
   =========================== */

  constructor(options) {
    super(options);
  }

  /** ===========================
   *     ÐÑÐ±Ð»Ð¸ÑÐ½ÑÐµ Ð¼ÐµÑÐ¾Ð´Ñ      *
   =========================== */

  showSlides(n) {
    /**
     * Ð¿Ð¾ÐºÐ°Ð·ÑÐ²Ð°ÐµÑ ÑÐ»Ð°Ð¹Ð´
     * n -> Ð½Ð¾Ð¼ÐµÑ ÑÐ»Ð°Ð¹Ð´Ð°, type: number
     */
    if (n > this.slides.length) {
      this.slideIndex = 1;
    }

    if (n < 1) {
      this.slideIndex = this.slides.length;
    }

    try {
      // Ð¼Ð¾Ð´ÐµÑÐ½Ð¸Ð·Ð°ÑÐ¸Ñ Ð´Ð»Ñ Ð²ÑÐ¿Ð»ÑÐ²Ð°ÑÑÐµÐ³Ð¾ Ð±Ð»Ð¾ÐºÐ°
      this.hanson.style.opacity = '0';

      if (n === 3){
        this.hanson.classList.add('animated');

        setTimeout(() => {
          this.hanson.style.opacity = '1';
          this.hanson.classList.add('slideInUp');
        }, 3000);
      } else {
        this.hanson.classList.remove('slideInUp');
      }
    }catch(e){
      console.info('Slider : ' + e.message)
    }

    Array.from(this.slides).forEach(slide => {
      slide.style.display = 'none';
    });

    this.slides[this.slideIndex - 1].style.display = 'block';
  }

  plusSlides(n) {
    /**
     * Ð¿ÐµÑÐµÐºÐ»ÑÑÐ°ÐµÑ ÑÐ»Ð°Ð¹Ð´Ñ
     * n -> Ð½Ð¾Ð¼ÐµÑ ÑÐ»Ð°Ð¹Ð´Ð°, type: number
     */
    this.showSlides(this.slideIndex += n);
  }

  render() {
    /**
     * Ð¸Ð½Ð¸ÑÐ¸Ð°Ð»Ð¸Ð·Ð¸ÑÑÐµÑ ÑÐ»Ð°Ð¹Ð´ÐµÑ
     */
    try {
      // Ð¼Ð¾Ð´ÐµÑÐ½Ð¸Ð·Ð°ÑÐ¸Ñ Ð´Ð»Ñ Ð²ÑÐ¿Ð»ÑÐ²Ð°ÑÑÐµÐ³Ð¾ Ð±Ð»Ð¾ÐºÐ°
      this.hanson = document.querySelector('.hanson');
    } catch(e){
      console.info('Slider : ' + e.message)
    }

    this.btns.forEach(item => {
      item.addEventListener('click', () => {
        this.plusSlides(1);
      });

      item.parentNode.previousElementSibling.addEventListener('click', (e) => {
        e.preventDefault();
        this.slideIndex = 1;
        this.showSlides(this.slideIndex);
      });
    });

    this.showSlides(this.slideIndex);
  }

}