export default class Difference {
  /**
   * Ð ÐµÐ°Ð»Ð¸Ð·Ð°ÑÐ¸Ñ Ð±Ð»Ð¾ÐºÐ° Ñ ÑÐ°Ð·Ð»Ð¸ÑÐ¸ÑÐ¼Ð¸
   *
   * oldOfficer   -> ÑÐµÐ»ÐµÐºÑÐ¾Ñ Ð¿ÐµÑÐ²ÑÐ¹ Ð±Ð»Ð¾Ðº
   * newOfficer   -> ÑÐµÑÐµÐºÑÐ¾Ñ Ð²ÑÐ¾ÑÐ¾Ð¹ Ð±Ð»Ð¾Ðº
   * items        -> ÑÐµÐ»ÐµÐºÑÐ¾Ñ Ð¿ÑÐ½ÐºÑÐ¾Ð² Ð±Ð»Ð¾ÐºÐ°
   */

  /** ===========================
   *        ÐÐ¾Ð½ÑÑÑÑÐºÑÐ¾Ñ        *
   =========================== */
  constructor(oldOfficer, newOfficer, items) {
    this.oldOfficer = document.querySelector(oldOfficer);
    this.newOfficer = document.querySelector(newOfficer);
    this.oldItems = this.oldOfficer.querySelectorAll(items);
    this.newItems = this.newOfficer.querySelectorAll(items);
    this.oldCounter = 0;
    this.newCounter = 0;
  }

  /** ===========================
   *     ÐÑÐ±Ð»Ð¸ÑÐ½ÑÐµ Ð¼ÐµÑÐ¾Ð´Ñ      *
   =========================== */
  bindTriggers(container, items, counter) {
    /**
     * Ð¿ÑÐ¸ ÐºÐ»Ð¸ÐºÐµ Ð½Ð° ÑÑÐ¸Ð³Ð³ÐµÑ Ð¿Ð¾ÐºÐ°Ð·ÑÐ²Ð°ÐµÑ Ð¿ÑÐ½ÐºÑ
     */
    container.querySelector('.plus').addEventListener('click', () => {
      if (counter !== items.length - 2) {
        items[counter].style.display = 'flex';
        counter++;
      } else {
        items[counter].style.display = 'flex';
        items[items.length - 1].remove();
      }
    });
  }

  hideItems(items) {
    /**
     * ÑÐºÑÑÐ²Ð°ÐµÑ Ð¿ÑÐ½ÐºÑÑ (ÐºÑÐ¾Ð¼Ðµ Ð¿Ð¾ÑÐ»ÐµÐ´Ð½ÐµÐ³Ð¾)
     */
    items.forEach((item, i, arr) => {
      if (i !== arr.length - 1) {
        item.style.display = 'none';
      }
    });
  }

  init() {
    /**
     * Ð¸Ð½Ð¸ÑÐ¸Ð°Ð»Ð¸Ð·Ð¸ÑÑÐµÑ Ð±Ð»Ð¾ÐºÐ¸ Ñ ÑÐ°Ð·Ð»Ð¸ÑÐ¸ÑÐ¼Ð¸
     */
    this.hideItems(this.oldItems);
    this.hideItems(this.newItems);

    this.bindTriggers(this.oldOfficer, this.oldItems, this.oldCounter);
    this.bindTriggers(this.newOfficer, this.newItems, this.newCounter);
  }
}