import Slider from './slider';

export default class MiniSlider extends Slider {
  /**
   * ÐÐ¢ÐÐ ÐÐ¡Ð¢ÐÐÐÐÐÐ«Ð Ð¡ÐÐÐÐÐÐ 
   *
   * options   -> Ð¾Ð¿ÑÐ¸Ð¸ Ð´Ð»Ñ ÑÐ°Ð±Ð¾ÑÑ ÑÐ»Ð°Ð¹Ð´ÐµÑÐ° (ÑÐ¼Ð¾ÑÑÐ¸ÑÐµ Ð¿Ð¾Ð´ÑÐ¾Ð±Ð½Ð¾ Ð² ÐºÐ»Ð°ÑÑÐµ Slider)
   */

  /** ===========================
   *        ÐÐ¾Ð½ÑÑÑÑÐºÑÐ¾Ñ        *
   =========================== */

  constructor(option) {
    super(option);
  }

  /** ===========================
   *     ÐÑÐ±Ð»Ð¸ÑÐ½ÑÐµ Ð¼ÐµÑÐ¾Ð´Ñ      *
   =========================== */

  decorizeSlides() {
    /**
     * Ð·Ð°Ð´Ð°ÐµÑ ÐºÐ»Ð°ÑÑ Ð°ÐºÑÐ¸Ð²Ð½Ð¾ÑÑÑ Ð¸ Ð°Ð½Ð¸Ð¼Ð°ÑÐ¸Ñ Ð°ÐºÑÐ¾Ð²Ð½Ð¾Ð³Ð¾ ÑÐ»Ð°Ð¹Ð´Ð°
     */
    Array.from(this.slides).forEach(slide => {

      slide.classList.remove(this.activeClass);

      if (this.animate) {
        slide.querySelector('.card__title').style.opacity = '0.4';
        slide.querySelector('.card__controls-arrow').style.opacity = '0';
      }

    });

    this.slides[0].classList.add(this.activeClass);

    if (this.animate) {
      this.slides[0].querySelector('.card__title').style.opacity = '1';
      this.slides[0].querySelector('.card__controls-arrow').style.opacity = '1';
    }

  }

  bindTriggers() {
    /**
     * Ð¿ÑÐ¸ ÐºÐ»Ð¸ÐºÐµ Ð½Ð° ÑÑÐ¸Ð³Ð³ÐµÑ(ÐºÐ½Ð¾Ð¿ÐºÐ¸) Ð¿ÐµÑÐµÐºÐ»ÑÑÐ°ÐµÑ ÑÐ»Ð°Ð¹Ð´ÐµÑ
     */
    this.next.addEventListener('click', () => {
      this.container.append(this.slides[0]);

      this.decorizeSlides()
    });

    this.prev.addEventListener('click', () => {
      let active = this.slides[this.slides.length -1];
      this.container.prepend(active);

      this.decorizeSlides()
    });
  }

  init() {
    /**
     * Ð¸Ð½Ð¸ÑÐ¸Ð°Ð»Ð¸Ð·Ð¸ÑÑÐµÑ ÑÐ»Ð°Ð¹Ð´ÐµÑ
     */
    this.container.style.cssText = `
            display: flex;
            flex-wrap: wrap;
            overflow: hidden;
            align-items: flex-start;
        `;

    this.bindTriggers();
    this.decorizeSlides();

    if (this.autoplay) {
      // Ð°Ð²ÑÐ¾Ð²Ð¾ÑÐ¿ÑÐ¾Ð¸Ð·Ð²ÐµÐ´ÐµÐ½Ð¸Ðµ

      setInterval(() =>{
        this.next.click();
      } , 5000);

    }

  }
}