import ValidatorForm from "../core/validatorForm";

export default class Form {
  /**
   * ÐÐÐÐ£ÐÐ¬ ÐÐÐ¯ Ð ÐÐÐÐ¢Ð« Ð¡ Ð¤ÐÐ ÐÐÐÐ
   *
   * forms   -> ÑÐµÐ»ÐµÐºÑÐ¾Ñ ÑÐ¾ÑÐ¼
   */

  /** ===========================
   *        ÐÐ¾Ð½ÑÑÑÑÐºÑÐ¾Ñ        *
   =========================== */

  constructor(forms) {
    this.forms = document.querySelectorAll(forms);
    this.inputs = document.querySelectorAll('input');
    this.message = {
      loading: 'ÐÐ°Ð³ÑÑÐ·ÐºÐ°...',
      success: 'Ð¡Ð¿Ð°ÑÐ¸Ð±Ð¾! Ð¡ÐºÐ¾ÑÐ¾ Ð¼Ñ Ñ Ð²Ð°Ð¼Ð¸ ÑÐ²ÑÐ¶ÐµÐ¼ÑÑ!',
      failure: 'Ð§ÑÐ¾-ÑÐ¾ Ð¿Ð¾ÑÐ»Ð¾ Ð½Ðµ ÑÐ°Ðº...'
    };
    this.path = 'assets/question.php';
  }

  /** ===========================
   *     ÐÑÐ±Ð»Ð¸ÑÐ½ÑÐµ Ð¼ÐµÑÐ¾Ð´Ñ      *
   =========================== */

  clearInputs() {
    /**
     * ÑÐ±ÑÐ°ÑÑÐ²Ð°ÐµÐ¼ Ð´Ð°Ð½Ð½ÑÐµ ÑÐ¾ÑÐ¼Ñ
     */
    this.inputs.forEach(item => {
      item.value = '';
    });
  }

  async postData(url, data) {
    /**
     * ÐÑÐ¸Ð½ÑÑÐ¾Ð½Ð½ÑÐ¹ Ð·Ð°Ð¿ÑÐ¾Ñ Ðº ÑÐ¾ÑÐ²ÐµÑÑ Ð¼ÐµÑÐ¾Ð´Ð¾Ð¼ POST
     * url   -> ÑÑÑÐ»ÐºÐ° Ð½Ð° ÑÐµÑÑÑÑ
     * data  -> Ð´Ð°Ð½Ð½ÑÐµ Ð´Ð»Ñ Ð¿ÐµÑÐµÐ´Ð°ÑÐ¸ Ð½Ð° ÑÐµÑÐ²ÐµÑ
     */
    let res = await fetch(url, {
      method: "POST",
      body: data
    });

    return await res.text();
  }

  init() {
    /**
     * Ð¸Ð½Ð¸ÑÐ¸Ð°Ð»Ð¸Ð·Ð¸ÑÑÐµÑ Ð¼Ð¾Ð´ÑÐ»Ñ ÑÐ°Ð±Ð¾ÑÑ Ð² ÑÐ¾ÑÐ¼Ð¾Ð¹
     */
    ValidatorForm.checkMailInputs('[type="email"]')
    ValidatorForm.initMask('[name="phone"]');

    this.forms.forEach(item => {
      item.addEventListener('submit', (e) => {
        e.preventDefault();

        let statusMessage = document.createElement('div');
        statusMessage.style.cssText = `
                    margin-top: 15px;
                    font-size: 18px;
                    color: grey;
                `;
        item.parentNode.appendChild(statusMessage);

        statusMessage.textContent = this.message.loading;

        const formData = new FormData(item);

        this.postData(this.path, formData)
          .then(res => {
            console.log(res);
            statusMessage.textContent = this.message.success;
          })
          .catch(() => {
            statusMessage.textContent = this.message.failure;
          })
          .finally(() => {
            this.clearInputs();
            setTimeout(() => {
              statusMessage.remove();
            }, 6000);
          });
      });
    });
  }
}